.artist-slide {
    padding: $padding-medium;
    background: $swatch-alto;

    &__body {
        height:     100%;
        position: relative;
        display: flex;
        flex-direction: column;
    }

    &__heading {
        font-size:      pxToRem(28);
        font-weight:    $font-weight-base;
        margin:         0;
        padding:        $padding-small;
        padding-left:   0;
        text-transform: uppercase;
        text-decoration: none;
    }

    &__type {
        font-size:      pxToRem(20);
        display:        block;
    }

    &__background {
        flex: 2;
        position: relative;
    }

    &__link {
      text-decoration: none;
      color: inherit;  
    }

    &__content {
        max-width: pxToRem(1080);
        margin: 0 auto;
        width: 100%;
        display: flex;
        flex-direction: row;

        h2 {
            text-transform: uppercase;
            font-weight: 400;

            em {
                margin-right: 60px;
                font-style: normal;
            }
        }

        ul {
            padding: 0;
            margin: 0;
            list-style: none;

            em {
                margin-right: 60px;
                font-style: normal; 
            }
        }
    }
}

.slide-background {

    &__image {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-size: cover;
        background-position: 50%;
    }

    &__play {
        background: $swatch-white;
        -webkit-mask: url(../../img/svg/play-circle.svg) no-repeat 50% 50%;
        mask: url(../../img/svg/play-circle.svg) no-repeat 50% 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        height: 32px;
        width: 32px;
        transform: translate(-50%, -50%);

        &.active {
            display: none;
        }
    }

    &__video {
        display: none;
        position: absolute;
        width: 100%;
        height: 100%;

        &.active {
            display: block;
        }
    }
}

/*
MEDIUM
*/

@media screen and (min-width: $medium-breakpoint)  {
    .artist-slide {
        background: none;
    
        &__body {
            background: $swatch-alto;
            padding: $padding-medium;
        }

        &__heading {
            text-align: center;
        }

        &__type {
            display: inline;
        }

        &__background {
            max-width: pxToRem(1080);
            left: 50%;
            transform: translate(-50%, 0);
            min-height: pxToRem(350);
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}

.content-details {
    padding-bottom:$padding-medium;

    &__left {
        margin-right: pxToRem(100);

        ul {
            text-transform: uppercase;

            li {
                margin-bottom: 3px;
            }
        }
    }

    &__right {

        ul {

            li {
                margin-bottom: 3px;
            }
        }        
    }
}
